import React from 'react';
import {Col, Row} from 'react-bootstrap';

import Bannercont from '../components/Bannercont';
import About from '../components/About';
import Backtoproj from '../components/Backtoproj';
import Mobileimgs from '../components/Mobileimgs';
import ProjectVideo from '../components/ProjectVideo';
import Dividemobilesmall from '../components/Dividemobilesmall';
import Dividedesktop from '../components/Dividedesktop';
import Fromleft from '../components/Fromleft';
import Fromright from '../components/Fromright';

import Bannerimage from '../images/wns/banner.jpg';
import Video from '../images/wns/wns.mp4';
import image1 from '../images/wns/1.jpg';
import image2 from '../images/wns/2.jpg';
import image3 from '../images/wns/3.jpg';
import image4 from '../images/wns/4.jpg';
import mobimg1 from '../images/wns/5.jpg';
import mobimg2 from '../images/wns/6.jpg';
import mobimg3 from '../images/wns/7.jpg';

export default class ProjectLastfm extends React.Component {
	/*scroll to top on page load*/
	componentDidMount() {
		 window.scrollTo(0, 0)
	}
  render() {
    return (
  	<div className='project_wrapper'>
			<Bannercont
				bgimage={Bannerimage}
				line1="Why Not Smile"
				line2="2016"
				line3="HTML CSS Javascript"
				line4="http://whynotsmile.com"
				
				/>
				<About>
        WNS (a.k.a. Why Not Smile) is a multidisciplinary creative consultancy based in New York City.
				</About>
				<div className='project_body'>
					<Row>
						<Col 
							md={3} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromleft 
								
							>
								<span 
									className='project_smallfont'
								>
									OVERVIEW
								</span>
								<br/><br/>
                The first objective was to use the Supersized full screen background slideshow jquery plugin
                to edit the functionality of the main site including the scaling, transitions, and touch-screen swiping.
                <br/><br/>
                The second objective was creating the overlay box which was initially shown on 
                page load, then later moved to the 'About WNS' link.
							</Fromleft>
							<br/><br/>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={6} 
							mdOffset={1} 
							sm={10}
							smOffset={1}
							xs={10} 
							xsOffset={1}>
							<ProjectVideo video={Video} poster={image1}/>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={7} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
							<Fromleft 
								
							>
								<img 
									src={image2} 
									className='project_fillimage'
									alt=""
								/>
							</Fromleft>
							<Dividedesktop/>
							<Dividemobilesmall/>
							<Row>
								<Col 
									md={6} 
									mdOffset={1} 
									sm={10} 
									smOffset={1} 
									xs={10} 
									xsOffset={1} 
									className='project_body_text'
								>
									<Fromleft 
										
									>
										The overlay box was primarily created using HTMl, CSS, and Javascript. The header with links at the top portion
                    is fixed with links that smooth scroll to various sections of the overlay box. The overlay box is responsive and extends
                    to become full-screen when the width of the device is smaller, such as in the case of a mobile device. All contents within
                    the overlay are also responsive and fit according to the screen width.
									</Fromleft>
								</Col>
							</Row>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={2} 
							mdOffset={1} 
							sm={4} 
							smOffset={4} 
							xs={10} 
							xsOffset={1}
						>
							<Fromright 
								
							>
								<img 
									src={image4} 
									className='project_fillimage' 
									alt=""
								/>
							</Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={8} 
							mdOffset={2} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
						<Fromleft 
							
						>
							<img 
								src={image3} 
								className='project_fillimage' 
								alt=""
							/>
						</Fromleft>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={4} 
							mdOffset={7} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromright 
								
							>
				        Notepad++, a free source code editor was used to make the changes to the main page of this site.
              </Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
				</div>
				<Mobileimgs 
					
					image1={mobimg1}
					image2={mobimg2}
					image3={mobimg3}
				/>
				<Backtoproj direction="/projects"/>
				</div>
    );
  }
}



