import React from 'react';
import {Col} from 'react-bootstrap';

export default class Banner extends React.Component {
  constructor(props) {
   super(props);
   this.state = {
     opacity: "0",
   }
 }

 componentWillUnmount(){
    if (this.timeout){
      clearTimeout(this.timeout);
    }
  }
  /*fades in the banner*/
  componentDidMount() {
   setTimeout(function() { this.setState({opacity:"1"}); }.bind(this), 450);
  }

  render() {
		const bannerunder={
      opacity:this.state.opacity,
		}

    return (
      <div
        style={bannerunder}
        className='project_banner_text'
      >
        <Col
          md={2}
          mdOffset={1}
          sm={10}
          smOffset={1}
          xs={10}
          xsOffset={1}
        >
          <strong>TITLE</strong>
          <br/>
          <span className='project_banner_text__small'>
            {this.props.line1}
          </span>
        </Col>

        <Col
          md={2}
          mdOffset={1}
          sm={10}
          smOffset={1}
          xs={10}
          xsOffset={1}
        >
          <strong>YEAR</strong>
          <br/>
          <span className='project_banner_text__small'>
            {this.props.line2}
          </span>
        </Col>

        <Col
          md={2}
          mdOffset={1}
          sm={10}
          smOffset={1}
          xs={10}
          xsOffset={1}
        >
          <strong>LANG</strong>
          <br/>
          <span className='project_banner_text__small'>
            {this.props.line3}
          </span>
        </Col>

        <Col
          md={2}
          mdOffset={1}
          sm={10}
          smOffset={1}
          xs={10}
          xsOffset={1}
        >
          <strong>LINK</strong>
          <br/>
          <span className='project_banner_text__small'>
            <a href={this.props.line4}>
              Link
            </a>
          </span>
        </Col>
      </div>
    );
  }
}



