import React from 'react';
import {Col, Row} from 'react-bootstrap';

import Bannercont from '../components/Bannercont';
import About from '../components/About';
import Backtoproj from '../components/Backtoproj';
import Mobileimgs from '../components/Mobileimgs';
import ProjectVideo from '../components/ProjectVideo';
import Dividemobilesmall from '../components/Dividemobilesmall';
import Dividedesktop from '../components/Dividedesktop';
import Fromleft from '../components/Fromleft';
import Fromright from '../components/Fromright';

import Bannerimage from '../images/template1/banner.jpg';
import Video from '../images/template1/template1.mp4';
import image1 from '../images/template1/1.jpg';
import image2 from '../images/template1/2.jpg';
import image3 from '../images/template1/3.jpg';
import image4 from '../images/template1/4.jpg';
import mobimg1 from '../images/template1/5.jpg';
import mobimg2 from '../images/template1/6.jpg';
import mobimg3 from '../images/template1/7.jpg';

export default class ProjectTemplate extends React.Component {
	/*scroll to top on page load*/
	componentDidMount() {
		 window.scrollTo(0, 0)
	}
  render() {
    return (
  	<div className='project_wrapper'>
			<Bannercont
				bgimage={Bannerimage}
				line1="React Template 1"
				line2="2017"
				line3="Javascript React"
				line4="http://shodyra1.herokuapp.com/"
				
				/>
				<About>
        This template was built using React, an open-source Javascript library,
        and Node.js, an open-source Javascript run-time environment used for executing javascript code server-side.
				</About>
				<div className='project_body'>
					<Row>
						<Col 
							md={3} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromleft 
								
							>
								<span 
									className='project_smallfont'
								>
									OVERVIEW
								</span>
								<br/><br/>
                  The app was first initialized using create-react-app, which allows the creation of 
                  React apps with no configuration.
							</Fromleft>
							<br/><br/>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={6} 
							mdOffset={1} 
							sm={10}
							smOffset={1}
							xs={10} 
							xsOffset={1}>
							<ProjectVideo video={Video} poster={image1}/>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={7} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
							<Fromleft 
								
							>
								<img 
									src={image2} 
									className='project_fillimage'
									alt=""
								/>
							</Fromleft>
							<Dividedesktop/>
							<Dividemobilesmall/>
							<Row>
								<Col 
									md={6} 
									mdOffset={1} 
									sm={10} 
									smOffset={1} 
									xs={10} 
									xsOffset={1} 
									className='project_body_text'
								>
									<Fromleft 
										
									>

									</Fromleft>
								</Col>
							</Row>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={2} 
							mdOffset={1} 
							sm={4} 
							smOffset={4} 
							xs={10} 
							xsOffset={1}
						>
							<Fromright 
								
							>
								<img 
									src={image4} 
									className='project_fillimage' 
									alt=""
								/>
							</Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={8} 
							mdOffset={2} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
						<Fromleft 
							
						>
							<img 
								src={image3} 
								className='project_fillimage' 
								alt=""
							/>
						</Fromleft>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={4} 
							mdOffset={7} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromright 
								
							>
				                      
							</Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
				</div>
				<Mobileimgs 
					
					image1={mobimg1}
					image2={mobimg2}
					image3={mobimg3}
				/>
				<Backtoproj direction="/projects"/>
				</div>
    );
  }
}



