import React from 'react';
import {Col, Row} from 'react-bootstrap';

import Bannercont from '../components/Bannercont';
import About from '../components/About';
import Backtoproj from '../components/Backtoproj';
import Mobileimgs from '../components/Mobileimgs';
import ProjectVideo from '../components/ProjectVideo';
import Dividemobilesmall from '../components/Dividemobilesmall';
import Dividedesktop from '../components/Dividedesktop';
import Fromleft from '../components/Fromleft';
import Fromright from '../components/Fromright';

import Bannerimage from '../images/wildcat/banner.jpg';
import Video from '../images/wildcat/vid.mp4';
import image1 from '../images/wildcat/1.jpg';
import image2 from '../images/wildcat/2.jpg';
import image3 from '../images/wildcat/3.jpg';
import image4 from '../images/wildcat/4.png';
import mobimg1 from '../images/wildcat/5.jpg';
import mobimg2 from '../images/wildcat/6.jpg';
import mobimg3 from '../images/wildcat/7.jpg';

export default class ProjectWildcat extends React.Component {
	/*scroll to top on page load*/
	componentDidMount() {
		 window.scrollTo(0, 0)
	}
  render() {
    return (
  	<div className='project_wrapper'>
			<Bannercont
				bgimage={Bannerimage}
				line1="University of Waterloo"
				line2="2017"
				line3="Javascript React"
				line4="https://ashleywildcat.com/"
				
				/>
				<About>
          Ashley Wildcat is a session wrestler and model. She is a member of Ring Card Girls,
          Fitness Models international, and USWO Wrestling.   She has worked with Ladies of Wrestling. Pro Wrestling Chicago,
          and ESport Fitness Batavia. She worked with Eternal Elegance and appeared in BeautyLook Magazine.
				</About>
				<div className='project_body'>
					<Row>
						<Col 
							md={3} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromleft 
								
							>
								<span 
									className='project_smallfont'
								>
									OVERVIEW
								</span>
								<br/><br/>
                Ashley Wildcat is an independent model and session wrestler. Her old static and unresponsive site was updated
                to a new site which is responsive to varying window and device sizes. The design of the site was heavily based around
                the color gold.
							</Fromleft>
							<br/><br/>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={6} 
							mdOffset={1} 
							sm={10}
							smOffset={1}
							xs={10} 
							xsOffset={1}>
							<ProjectVideo video={Video} poster={image1}/>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={7} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
							<Fromleft 
								
							>
								<img 
									src={image2} 
									className='project_fillimage'
									alt=""
								/>
							</Fromleft>
							<Dividedesktop/>
							<Dividemobilesmall/>
							<Row>
								<Col 
									md={6} 
									mdOffset={1} 
									sm={10} 
									smOffset={1} 
									xs={10} 
									xsOffset={1} 
									className='project_body_text'
								>
									<Fromleft 
										
									>
										React-router animations wer eheavily used, as every page is a component sliding in from a different direction.
                    Clicking on sessions introduces the new page by sliding in three panel components from the top, which gives the site a more
                    cohesive feeling, such that every piece of the site seems connected to one another.
									</Fromleft>
								</Col>
							</Row>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={2} 
							mdOffset={1} 
							sm={4} 
							smOffset={4} 
							xs={10} 
							xsOffset={1}
						>
							<Fromright 
								
							>
								<img 
									src={image4} 
									className='project_fillimage' 
									alt=""
								/>
							</Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={8} 
							mdOffset={2} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
						<Fromleft 
							
						>
							<img 
								src={image3} 
								className='project_fillimage' 
								alt=""
							/>
						</Fromleft>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={4} 
							mdOffset={7} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromright 
								
							>
								The site was coded on Visual Studio Code with Javascript in the React Framework. The contact form
                uses an Express server backend which is connected to the main site by a reverse proxy on NGINX.
							</Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
				</div>
				<Mobileimgs 
					
					image1={mobimg1}
					image2={mobimg2}
					image3={mobimg3}
				/>
				<Backtoproj direction="/projects"/>
				</div>
    );
  }
}



