import React from 'react';
import { connect } from 'react-redux';

class Banner extends React.Component {
  constructor(props) {
	 super(props);
	 this.state = {
     opacity: "0",
     offsettop:"0",
   }
   this.movebanner=this.movebanner.bind(this);
 }
  /*fades in the banner*/
  componentDidMount() {
  this.timeout = setTimeout(function() { this.setState({opacity:"1"}); }.bind(this), 450);
   window.addEventListener("scroll", this.movebanner);
   window.addEventListener("resize", this.movebanner);
   this.movebanner();

  }


  componentWillUnmount(){
    if (this.timeout){
    clearTimeout(this.timeout);
    }
  }

  movebanner(){
    try{
      if (this.props.scrollamount <270){
        this.setState({
          offsettop: this.props.scrollamount
        });
      }
     else{
        this.setState({
          offsettop:"270px"
        });
      }
    }
    catch(err) {
  }
}

  render() {
		const projectBannerImage={
      marginTop:this.state.offsettop,
      opacity: this.state.opacity,
			background: 'url('+this.props.bgimage+')',
			backgroundRepeat: "no-repeat",
			backgroundPosition: "center top",
			backgroundSize: "cover",
		}
    return (
      <div
        style={projectBannerImage}
        className='project_banner_image'
      >
      </div>
    );
  }
}

export default connect(
  (state, ownProps) => ({
    scrollamount: state.scroll.scrollAmount,
  }),
  dispatch => ({
  }),
)(Banner);



