import React from 'react';

import {Row, Col} from 'react-bootstrap';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import * as MenuActions from '../actions/projects';
import classNames from 'classnames';

import tcfs from '../images/thumbnails/tcfs.jpg';
import leida from '../images/thumbnails/leida.jpg';
import discord from '../images/thumbnails/discord.jpg';
import wns from '../images/thumbnails/wns.jpg';

import phpgame from '../images/thumbnails/phpgame.jpg';
import template1 from '../images/thumbnails/template1.jpg';
import drjart from '../images/thumbnails/drjart.jpg';
import library from '../images/thumbnails/library.jpg';

import powerpcme from '../images/thumbnails/powerpcme.jpg';
import wildcat from '../images/thumbnails/wildcat.jpg';
import lastfm from '../images/thumbnails/lastfm.jpg';
import uwloo from '../images/thumbnails/uwloo.jpg';

import Thumbnail from '../components/Thumbnail';
import debounce from 'debounce';

class Projects extends React.Component{
  constructor(props) {
    super(props);
    this.state = {
      transition:".5s ease-out"
    }
    this.bgresize=this.bgresize.bind(this)
    this.restoretrans = debounce(this.restoretrans.bind(this), 100);

  }


	bgresize(){
		this.setState({
			transition:"0s"
		}, () => {
			this.restoretrans();
	});
	}
	/*when resizing the window, the transition time is 0s so the blackbg doesn't bounce around, then it is set back to 600ms with a debounce so there is delay*/

	restoretrans(){
		this.setState({
			transition:".5s ease-out"
		})
	}


  componentDidMount(){
    this.props.action.nextPage(0)
    window.addEventListener("resize", this.bgresize);
  }

  componentWillUnmount(){
    window.removeEventListener("resize", this.bgresize);
  }
  render(){

    const transitiontime={
      WebkitTransition: this.state.transition,
			MozTransition: this.state.transition,
			OTransition: this.state.transition,
			transition:this.state.transition,
    }
    const projectsWrapper = classNames(
      'projects_wrapper',
      {
        'projects_wrapper--display':this.props.isGaming
      }
    );
    const projectThumbnail1 = classNames(
      'projects_thumbnail1',
      {
        'projects_thumbnail--display':this.props.isProjects
      }
    );
    const projectThumbnail2 = classNames(
      'projects_thumbnail2',
      {
        'projects_thumbnail--display':this.props.isProjects
      }
    );
    const projectThumbnail3 = classNames(
      'projects_thumbnail3',
      {
        'projects_thumbnail--display':this.props.isProjects
      }
    );
    const projectThumbnail4 = classNames(
      'projects_thumbnail4',
      {
        'projects_thumbnail--display':this.props.isProjects
      }
    );
    const projectLeft = classNames(
      'projects_left',
      {
        'projects_left--display':this.props.isProjects
      }
    )
    const projectRight = classNames(
      'projects_right',
      {
        'projects_right--display':this.props.isProjects
      }
    )
    const projectTextLeft = classNames(
      'project_thumbnail__text__left',
      {
        'project_thumbnail__text__left--display':this.props.isProjects
      }
    )
    const projectTextLeft2 = classNames(
      'project_thumbnail__text__left2',
      {
        'project_thumbnail__text__left2--display':this.props.isProjects
      }
    )
    const projectTextRight = classNames(
      'project_thumbnail__text__right',
      {
        'project_thumbnail__text__right--display':this.props.isProjects
      }
    )

    return(
      <div className={projectsWrapper}>
        <Row>
          <Col md={6} className={projectThumbnail1} style={transitiontime}>
            <Thumbnail
            bgimage={this.props.currentPage === 1 ? uwloo :
              (this.props.currentPage=== 2 ? powerpcme : drjart)}

            link={this.props.currentPage === 1 ? '/projects/uwloo' :
              (this.props.currentPage=== 2 ? '/projects/powerpcme' : '/projects/drjart')}

            />
          </Col>
          <Col md={2} mdOffset={1} sm={12} smOffset={0} xs={12} xsOffset={0}>
            {this.props.currentPage === 1
            ?
            <div className={projectTextLeft}>
              Creation<br/>
              <strong>University of Waterloo Thermo-Fluids Seminar</strong><br/>
              Javascript React<br/>
              2017
            </div>
            :
              (this.props.currentPage=== 2
              ?
              <div className={projectTextLeft}>
              Creation<br/>
              <strong>PowerPCME website</strong><br/>
              Javascript React<br/>
              2017
              </div>
              :
              <div className={projectTextLeft}>
              Creation<br/>
              <strong>DR. Jart+ Newsletter</strong><br/>
              HTML CSS<br/>
              2016
              </div>
              )
            }
          </Col>
        </Row>
        <Row>
          <Col md={6} mdOffset={2} className={projectThumbnail2}>
            <Thumbnail
            bgimage={this.props.currentPage === 1 ? tcfs :
            (this.props.currentPage=== 2 ? leida : phpgame)}

            link={this.props.currentPage === 1 ? '/projects/tcfs' :
            (this.props.currentPage=== 2 ? '/projects/leida' : '/projects/phpgame')}
            />
          </Col>
          <Col md={2} mdOffset={1} sm={12} smOffset={0} xs={12} xsOffset={0}>
            {this.props.currentPage === 1
            ?
            <div className={projectTextLeft2}>
              Editing<br/>
              <strong>toocoolforschool.us</strong><br/>
              HTML CSS Javascript<br/>
              2016 - 2017
            </div>
            :
              (this.props.currentPage=== 2
              ?
              <div className={projectTextLeft2}>
              Creation<br/>
              <strong>ideal leida</strong><br/>
              HTML CSS Javascript PHP<br/>
              2016
              </div >
              :
              <div className={projectTextLeft2}>
              Creation<br/>
              <strong>PHP/SQL Game</strong><br/>
              HTML CSS Javascript PHP SQL<br/>
              2016
              </div>
              )
            }
          </Col>
        </Row>
        <Row>
          <Col md={3}>
              {this.props.currentPage === 1
              ?
              <div className={projectTextRight}>
                Creation<br/>
                <strong>Ashley Wildcat Website</strong><br/>
                Javascript Node.js React<br/>
                2017
              </div>
              :
                (this.props.currentPage=== 2
                ?
                <div className={projectTextRight}>
                Creation<br/>
                <strong>Last.fm Personal Project</strong><br/>
                Javascript Node.js React<br/>
                2017
                </div>
                :
                <div className={projectTextRight}>
                Creation<br/>
                <strong>WNS Library</strong><br/>
                HTML CSS<br/>
                2016
                </div>
                )
              }
            </Col>
          <Col md={6} mdOffset={1} className={projectThumbnail3}>
            <Thumbnail
            bgimage={this.props.currentPage === 1 ? wildcat :
            (this.props.currentPage=== 2 ? lastfm : library)}

            link={this.props.currentPage === 1 ? '/projects/wildcat' :
            (this.props.currentPage=== 2 ? '/projects/lastfm' : '/projects/library')}
            />
          </Col>
        </Row>
        <Row>
        <Col md={3} mdOffset={2}>
            {this.props.currentPage === 1
            ?
            <div className={projectTextRight}>
              Creation<br/>
              <strong>Discord Bot</strong><br/>
              Javascript Node.js<br/>
              2017
            </div>
            :
              (this.props.currentPage=== 2
              ?
              <div className={projectTextRight}>
              Editing<br/>
              <strong>Why Not Smile Homepage</strong><br/>
              HTML CSS Javascript<br/>
              2016
              </div>
              :
              <div className={projectTextRight}>
              Creation<br/>
              <strong>React Template 1</strong><br/>
              HTML CSS Javascript React<br/>
              2017
              </div>
              )
            }
          </Col>
          <Col md={6} mdOffset={1} className={projectThumbnail4}>
          <Thumbnail
          bgimage={this.props.currentPage === 1 ? discord :
          (this.props.currentPage=== 2 ? wns : template1)}

          link={this.props.currentPage === 1 ? '/projects/discord' :
          (this.props.currentPage=== 2 ? '/projects/wns' : '/projects/template1')}
          />
          </Col>
        </Row>

        <div
          className={projectLeft}
          onClick={()=>this.props.action.previousPage(this.props.currentPage)
        }>
        &lt;
        </div>
        <div
          className={projectRight}
          onClick={()=>this.props.action.nextPage(this.props.currentPage)}
        >
        &gt;
        </div>
      </div>
    )
  }
}

function mapStateToProps(state, prop){
  /*the name of the reducer*/
  return{
    isHidden:state.projects.isHidden,
    currentPage: state.projects.pageNumber,
  }
}

function mapDispatchToProps(dispatch){
  return {
    action: bindActionCreators(MenuActions, dispatch)
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Projects);


