import React from 'react';
import {Col, Row} from 'react-bootstrap';

import Bannercont from '../components/Bannercont';
import About from '../components/About';
import Backtoproj from '../components/Backtoproj';
import ProjectVideo from '../components/ProjectVideo';
import Dividemobilesmall from '../components/Dividemobilesmall';
import Dividedesktop from '../components/Dividedesktop';
import Fromleft from '../components/Fromleft';
import Fromright from '../components/Fromright';

import Bannerimage from '../images/phpgame/banner.jpg';
import Video from '../images/phpgame/vid.mp4';
import image1 from '../images/phpgame/1.jpg';
import image2 from '../images/phpgame/2.jpg';
import image3 from '../images/phpgame/3.jpg';
import image4 from '../images/phpgame/4.jpg';

export default class ProjectLastfm extends React.Component {
	/*scroll to top on page load*/
	componentDidMount() {
		 window.scrollTo(0, 0)
	}
  render() {
    return (
  	<div className='project_wrapper'>
			<Bannercont
				bgimage={Bannerimage}
				line1="PHP Game"
				line2="2017"
				line3="Javascript PHP SQL"
				line4="http://gentle-brushlands-39682.herokuapp.com"

				/>
				<About>
          This game is designed to be an easily expanded and edited RPG game where
          the user can recruit party members and use up to four of them in turn-based combat.
          This game is designed primarily with PHP and SQL, and is an exercise in PHP and SQL.
          SQL tables for large maps are filled in with lines generated by scripts written with
          Python. User passwords upon registration are encrypted with bcrypt.
				</About>
				<div className='project_body'>
					<Row>
						<Col
							md={3}
							mdOffset={1}
							sm={10}
							smOffset={1}
							xs={10}
							xsOffset={1}
							className='project_body_text'
						>
							<Fromleft

							>
								<span
									className='project_smallfont'
								>
									OVERVIEW
								</span>
								<br/><br/>
                Each user can have many different characters, which each have various stats such as Health, Attack, Defense,
                Fire Resistance, Ice Resistance, Life per Hit, and Thorns. These values are stored in relational databases using
                SQL. The maps are also stored on SQL and creating new maps is simply a matter of creating more values in the database.
                <br/><br/>
                Graphic assets are still not implemented, and this game is a work in progress using mostly placeholder graphics at
                the moment until character stats and combats is functional.
							</Fromleft>
							<br/><br/>
						</Col>
						<Dividemobilesmall/>
						<Col
							md={6}
							mdOffset={1}
							sm={10}
							smOffset={1}
							xs={10}
							xsOffset={1}>
							<ProjectVideo video={Video} poster={image1}/>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col
							md={7}
							mdOffset={1}
							sm={10}
							smOffset={1}
							xs={10}
							xsOffset={1}
						>
							<Fromleft

							>
								<img
									src={image2}
									className='project_fillimage'
									alt=""
								/>
							</Fromleft>
							<Dividedesktop/>
							<Dividemobilesmall/>
							<Row>
								<Col
									md={6}
									mdOffset={1}
									sm={10}
									smOffset={1}
									xs={10}
									xsOffset={1}
									className='project_body_text'
								>
									<Fromleft

									>
										When a user registers, their username and and password are added to a sql table after being encrypted with bcrypt.
                    They are then able to start a game, which sets coordinates to (50,50) on a 19x19 map starting from (40,40) to (59,59).
                    On tile (50,50), the user is able to 'recruit bob', which is a test character. A character 'bob' is added to the character table, which pulls stats from a stats table, and is given a user_id that matches the id of the user such that each user can have different versions of the same base 'bob' character.
                    Users are able to move across the map, which is stored in a 'map1' table, and the move functions allow users to step on tiles marked with type '1'.
                    The next step of progress is developing a combat system which will set a user's status as in combat or out of combat,
                    such that one turn-based battle occurs at a time and users cannot move from their tile until the battle is over.
									</Fromleft>
								</Col>
							</Row>
						</Col>
						<Dividemobilesmall/>
						<Col
							md={2}
							mdOffset={1}
							sm={4}
							smOffset={4}
							xs={10}
							xsOffset={1}
						>
							<Fromright

							>
								<img
									src={image4}
									className='project_fillimage'
									alt=""
								/>
							</Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col
							md={8}
							mdOffset={2}
							sm={10}
							smOffset={1}
							xs={10}
							xsOffset={1}
						>
						<Fromleft

						>
							<img
								src={image3}
								className='project_fillimage'
								alt=""
							/>
						</Fromleft>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col
							md={4}
							mdOffset={7}
							sm={10}
							smOffset={1}
							xs={10}
							xsOffset={1}
							className='project_body_text'
						>
							<Fromright

							>
				        This project is written using the Atom IDE with a focus on PHP and SQL.
              </Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
				</div>

				<Backtoproj direction="/projects"/>
				</div>
    );
  }
}



