import React from 'react';
import {Link} from 'react-router-dom';

export default class Thumbnail extends React.Component{
  render(){
    const thumbnailInner = {
      background: 'url('+this.props.bgimage+')',
    }

  return(

    <div className='projects_thumbnail__outer'>

    <Link to={this.props.link}>
      <div
      className='projects_thumbnail__inner'
      style={thumbnailInner}
    >
    </div>
    </Link>
    <div className='project_thumbnail__overlay'></div>
    </div>
    )
  }
}


