import React from 'react';
import {Col, Row} from 'react-bootstrap';

import Bannercont from '../components/Bannercont';
import About from '../components/About';
import Backtoproj from '../components/Backtoproj';
import Mobileimgs from '../components/Mobileimgs';
import ProjectVideo from '../components/ProjectVideo';
import Dividemobilesmall from '../components/Dividemobilesmall';
import Dividedesktop from '../components/Dividedesktop';
import Fromleft from '../components/Fromleft';
import Fromright from '../components/Fromright';

import Bannerimage from '../images/lastfm/banner.jpg';
import Video from '../images/lastfm/vid.mp4';
import image1 from '../images/lastfm/1.jpg';
import image2 from '../images/lastfm/2.jpg';
import image3 from '../images/lastfm/3.jpg';
import image4 from '../images/lastfm/4.png';
import mobimg1 from '../images/lastfm/5.jpg';
import mobimg2 from '../images/lastfm/6.jpg';
import mobimg3 from '../images/lastfm/7.jpg';

export default class ProjectLastfm extends React.Component {
	/*scroll to top on page load*/
	componentDidMount() {
		 window.scrollTo(0, 0)
	}
  render() {
    return (
  	<div className='project_wrapper'>
			<Bannercont
				bgimage={Bannerimage}
				line1="Last.fm API site"
				line2="2017"
				line3="Javascript React"
				line4="https://shodyra15.herokuapp.com/"
				/>
				<About>
          This personal project uses the last.fm API to get the currently playing or most recently played song of any user 
          live. When the song updates, there is a display of five similar songs, which users can click to display further similar songs. Users can 
          then click those songs to get similar songs to those.
				</About>
				<div className='project_body'>
					<Row>
						<Col 
							md={3} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromleft 
								
							>
								<span 
									className='project_smallfont'
								>
									OVERVIEW
								</span>
								<br/><br/>
                The site polls the Last.fm API on an interval to update the displayed track. If the user is listening to a new song, the displayed image will
                change without needing to refresh the page. If the image displayed changes, a function is called to find similar tracks to the currently playing track,
                and displays them on the side.
							</Fromleft>
							<br/><br/>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={6} 
							mdOffset={1} 
							sm={10}
							smOffset={1}
							xs={10} 
							xsOffset={1}>
							<ProjectVideo video={Video} poster={image1}/>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={7} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
							<Fromleft 
								
							>
								<img 
									src={image2} 
									className='project_fillimage'
									alt=""
								/>
							</Fromleft>
							<Dividedesktop/>
							<Dividemobilesmall/>
							<Row>
								<Col 
									md={6} 
									mdOffset={1} 
									sm={10} 
									smOffset={1} 
									xs={10} 
									xsOffset={1} 
									className='project_body_text'
								>
									<Fromleft 
										
									>
										Users can input any username into the input box to find the currently playing track. If the track's album art is unavailable, 
                    the image is not displayed. If similar tracks could not be found, the similar image slots are emptied. A debounce function was used 
                    to prevent calling the last.fm API at too high of a rate.
									</Fromleft>
								</Col>
							</Row>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={2} 
							mdOffset={1} 
							sm={4} 
							smOffset={4} 
							xs={10} 
							xsOffset={1}
						>
							<Fromright 
								
							>
								<img 
									src={image4} 
									className='project_fillimage' 
									alt=""
								/>
							</Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={8} 
							mdOffset={2} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
						<Fromleft 
							
						>
							<img 
								src={image3} 
								className='project_fillimage' 
								alt=""
							/>
						</Fromleft>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={4} 
							mdOffset={7} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromright 
								
							>
				        Clicking one of the similar-track images opens a panel which displays tracks that are similar to the clicked track.
                These tracks inside the panel are also clickable and will refresh the panel with similar tracks to the newly clicked track image.               
							</Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
				</div>
				<Mobileimgs 
					
					image1={mobimg1}
					image2={mobimg2}
					image3={mobimg3}
				/>
				<Backtoproj direction="/projects"/>
				</div>
    );
  }
}



