import React from 'react';
import {Col, Row} from 'react-bootstrap';

import Bannercont from '../components/BannercontGaming';
import About from '../components/About';
import Backtoproj from '../components/Backtoproj';
import ProjectVideo from '../components/ProjectVideo';
import Dividemobilesmall from '../components/Dividemobilesmall';
import Dividedesktop from '../components/Dividedesktop';
import Fromleft from '../components/Fromleft';
import Fromright from '../components/Fromright';

import Bannerimage from '../images/gaming/wow.jpg';
import image1 from '../images/gaming/wow1.png';
import image2 from '../images/gaming/wow2.png';
import image3 from '../images/gaming/wow3.png';
import image4 from '../images/gaming/wow4.png';

export default class GamingWow extends React.Component {
	/*scroll to top on page load*/
	componentDidMount() {
		 window.scrollTo(0, 0)
	}
  render() {
    return (
  	<div className='project_wrapper'>
			<Bannercont
				bgimage={Bannerimage}
				line1="World of Warcraft"
				line2="2009"
				line3="8th Worldwide"
				
				/>
				<About>
          World of Warcraft is an MMORPG (Massive Multiplayer Role Playing Game). An overall ranking of rank 8
          globally in the Arena format of World of Warcraft was achieved during the 'Wrath of the Lich King Expansion' (2008-2010).
          At the time, 2v2, 3v3, and 5v5 personal ratings were at approximately 2,800 to 2,900.
          Images shown are archived rankings from 'arenajunkies' and 'sk-gaming'.
          Unfortunately, statistics were reset upon faction change from Tauren/Horde to Draenei/Alliance as seen from discrepencies in the achievements page and statistics page.
          http://us.battle.net/wow/en/character/mannoroth/Shodire/statistic#21:152
        </About>
				<div className='project_body'>
					<Row>
						<Col 
							md={3} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromleft 
								
							>
								<span 
									className='project_smallfont'
								>
									OVERVIEW
								</span>
								<br/><br/>
                The peak rating was achieved as a Shaman on the 'Mug'thol' server, which is on the 'Vengeance' battlegroup.
                The 2v2 team composition was primarily Restoration Shaman/Retribution Paladin, and the 3v3 team composition toggled
                between 'Restoration Shaman/Retribution Paladin/Discipline Priest', 'Restoration Shaman/Retribution Paladin/Rogue',
                and 'Retribution Paladin/Rogue/Discipline Priest'; the Priest at the time was 'Sayn' and the Rogue was 'Rum'.
                <br/><br/>
                The account achieved peak rating under the name 'Myhope', which later changed to 'Relictus' on server transfer,
            and ultimately 'Lightspvp' on faction transfer. Faction change was done when the 'Will of the Forsaken' undead racial
            ability on horde became weakened, and the human racial 'Every Man for Himself' on the alliance side became more
            favorable for competitive PvPers (Player vs Player).
							</Fromleft>
							<br/><br/>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={6} 
							mdOffset={1} 
							sm={10}
							smOffset={1}
							xs={10} 
							xsOffset={1}>
							<ProjectVideo poster={image1}/>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={7} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
							<Fromleft 
								
							>
								<img 
									src={image2} 
									className='project_fillimage'
									alt=""
								/>
							</Fromleft>
							<Dividedesktop/>
							<Dividemobilesmall/>
							<Row>
								<Col 
									md={6} 
									mdOffset={1} 
									sm={10} 
									smOffset={1} 
									xs={10} 
									xsOffset={1} 
									className='project_body_text'
								>
									<Fromleft 
										
									>
										There exists an archive of June 2009 of 5v5 team personal ratings. A closer time frame archive is not available to my knowledge.
                    http://web.archive.org/web/20090618080239/http://www.arenajunkies.com/char/US/Mug'thol/Myhope/
									</Fromleft>
								</Col>
							</Row>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={2} 
							mdOffset={1} 
							sm={4} 
							smOffset={4} 
							xs={10} 
							xsOffset={1}
						>
							<Fromright 
								
							>
								<img 
									src={image4} 
									className='project_fillimage' 
									alt=""
								/>
							</Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={8} 
							mdOffset={2} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
						<Fromleft 
							
						>
							<img 
								src={image3} 
								className='project_fillimage' 
								alt=""
							/>
						</Fromleft>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={4} 
							mdOffset={7} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromright 
								
							>
				       At the time of the peak, World of Warcraft subscription was at its highest with between 
               11 million and 12 million active subscribers.
               <br/><br/>
               After playing with the Retribution Paladin known as 'Mention' or 'Emonerd' at the time, the character name was changed to 'Lightspvp'
               and transferred to the 'Retaliation' battlegroup and hovered between 2,600 and 2,800 rating while playing with a Warlock player named 'Lasers',
               achieving the statistics shown on my current account statistics page.
              </Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
				</div>

				<Backtoproj direction="/gaming"/>
				</div>
    );
  }
}



