import React from 'react';
import {Col, Row} from 'react-bootstrap';

import Bannercont from '../components/BannercontGaming';
import About from '../components/About';
import Backtoproj from '../components/Backtoproj';
import ProjectVideo from '../components/ProjectVideo';
import Dividemobilesmall from '../components/Dividemobilesmall';
import Dividedesktop from '../components/Dividedesktop';
import Fromleft from '../components/Fromleft';
import Fromright from '../components/Fromright';

import Bannerimage from '../images/gaming/ow.jpg';
import image1 from '../images/gaming/ow1.png';
import image2 from '../images/gaming/ow2.png';
import image3 from '../images/gaming/ow3.png';
import image4 from '../images/gaming/ow4.png';

export default class GamingOw extends React.Component {
	/*scroll to top on page load*/
	componentDidMount() {
		 window.scrollTo(0, 0)
	}
  render() {
    return (
  	<div className='project_wrapper'>
			<Bannercont
				bgimage={Bannerimage}
				line1="Overwatch"
				line2="2016"
				line3="Diamond League"
				
				/>
				<About>
        Overwatch, the most recent game developed by Blizzard Entertainment has an active playerbase of 20 million as of October 2016 according to Polygon Link. A
        rank of diamond was reached. Due to the unavailability of a Blizzard API or public database for the users for Overwatch, the exact percentile is unknown.
          
        </About>
				<div className='project_body'>
					<Row>
						<Col 
							md={3} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromleft 
								
							>
								<span 
									className='project_smallfont'
								>
									OVERVIEW
								</span>
								<br/><br/>
                Overwatch is an FPS (First Person Shooter) game where there are a variety of characters each with different
                abilities played on 6v6 maps to complete specific objectives.
                Similar to League of Legends, different characters fulfill different roles such as Tank, Support, or Damage.
                Objectives are generally to work as a team to either capture a point on the map from the opposing team or to
                escort a payload to the other side of the map while the opposing team attempts to stop your progress.
							</Fromleft>
							<br/><br/>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={6} 
							mdOffset={1} 
							sm={10}
							smOffset={1}
							xs={10} 
							xsOffset={1}>
							<ProjectVideo poster={image1}/>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={7} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
							<Fromleft 
								
							>
								<img 
									src={image2} 
									className='project_fillimage'
									alt=""
								/>
							</Fromleft>
							<Dividedesktop/>
							<Dividemobilesmall/>
							<Row>
								<Col 
									md={6} 
									mdOffset={1} 
									sm={10} 
									smOffset={1} 
									xs={10} 
									xsOffset={1} 
									className='project_body_text'
								>
									<Fromleft 
										
									>
									In the climb to a diamond rating, I generally took on a 'fill' position, filling in any role that was needed, whether
                  it be tank, healer or damage. The characters that were picked were Reinhardt, Lucio, and Soldier 76, which are tank, healer,
                  and damage respectively. These characters seemed to fit the most into any team composition, whereas others such as sniper positions
                  fulfilled much more niche and self-serving roles.
									</Fromleft>
								</Col>
							</Row>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={2} 
							mdOffset={1} 
							sm={4} 
							smOffset={4} 
							xs={10} 
							xsOffset={1}
						>
							<Fromright 
								
							>
								<img 
									src={image4} 
									className='project_fillimage' 
									alt=""
								/>
							</Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={8} 
							mdOffset={2} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
						<Fromleft 
							
						>
							<img 
								src={image3} 
								className='project_fillimage' 
								alt=""
							/>
						</Fromleft>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={4} 
							mdOffset={7} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromright 
								
							>
				      Due to personal lack of experience with FPS (First Person Shooter) games, game knowledge and strategy was necessary
              to achieve a reasonably high ranking.
              </Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
				</div>

				<Backtoproj direction="/gaming"/>
				</div>
    );
  }
}



