import React from 'react';

import {Link} from 'react-router-dom';

/*back to projects page or gaming page from individual projects/gaming pages, hidden on phone sized devices*/
export default class Backtoproj extends React.Component {
  render() {
    return (
			<div className='project_back'>
				<div className='project_back__inner'>
					<Link
						to={this.props.direction}
						className='project_back__link'
					>
						&larr; BACK
					</Link>
				</div>
			</div>
    );
  }
}



