import React from 'react';
import {Col, Row} from 'react-bootstrap';

import Bannercont from '../components/Bannercont';
import About from '../components/About';
import Backtoproj from '../components/Backtoproj';
import ProjectVideo from '../components/ProjectVideo';
import Dividemobilesmall from '../components/Dividemobilesmall';
import Dividedesktop from '../components/Dividedesktop';
import Fromleft from '../components/Fromleft';
import Fromright from '../components/Fromright';

import Bannerimage from '../images/library/banner.jpg';
import Video from '../images/library/library.mp4';
import image1 from '../images/library/1.jpg';
import image2 from '../images/library/2.jpg';
import image3 from '../images/library/1.jpg';
import image4 from '../images/library/4.jpg';

export default class ProjectLibrary extends React.Component {
	/*scroll to top on page load*/
	componentDidMount() {
		 window.scrollTo(0, 0)
	}
  render() {
    return (
  	<div className='project_wrapper'>
			<Bannercont
				bgimage={Bannerimage}
				line1="WNS Library"
				line2="2016"
				line3="HTML CSS"
				line4="http://wnslibrary.tumblr.com"
				
				/>
				<About>
          WNS Library is a visual collection of Why Not Smile's books.
				</About>
				<div className='project_body'>
					<Row>
						<Col 
							md={3} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromleft 
								
							>
								<span 
									className='project_smallfont'
								>
									OVERVIEW
								</span>
								<br/><br/>
                WNS Library was built on the tumblr platform using 'Otlet's Shelf' plugin.
                The format was edited with CSS.
							</Fromleft>
							<br/><br/>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={6} 
							mdOffset={1} 
							sm={10}
							smOffset={1}
							xs={10} 
							xsOffset={1}>
							<ProjectVideo video={Video} poster={image1}/>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={7} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
							<Fromleft 
								
							>
								<img 
									src={image2} 
									className='project_fillimage'
									alt=""
								/>
							</Fromleft>
							<Dividedesktop/>
							<Dividemobilesmall/>
							<Row>
								<Col 
									md={6} 
									mdOffset={1} 
									sm={10} 
									smOffset={1} 
									xs={10} 
									xsOffset={1} 
									className='project_body_text'
								>
									<Fromleft 
										
									>
										The search bar was implemented to search through various tags which were added to each book.
									</Fromleft>
								</Col>
							</Row>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={2} 
							mdOffset={1} 
							sm={4} 
							smOffset={4} 
							xs={10} 
							xsOffset={1}
						>
							<Fromright 
								
							>
								<img 
									src={image4} 
									className='project_fillimage' 
									alt=""
								/>
							</Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={8} 
							mdOffset={2} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
						<Fromleft 
							
						>
							<img 
								src={image3} 
								className='project_fillimage' 
								alt=""
							/>
						</Fromleft>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={4} 
							mdOffset={7} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromright 
								
							>
				        HTML/CSS was used to edit features on the tumblr platform.
              </Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
				</div>

				<Backtoproj direction="/projects"/>
				</div>
    );
  }
}



