import {
  NEXT_PAGE,
  PREVIOUS_PAGE,
} from '../actions/gaming'

const DEFAULT_STATE={
  isHidden:true,
  pageNumber:1,
}

export default(state=DEFAULT_STATE, payload)=>
{
  switch(payload.type){
    case NEXT_PAGE:
      if (payload.pageNumber >= 2){
        return state = {...state, pageNumber: 1}
      }
      return state = {...state, pageNumber: payload.pageNumber + 1}
    case PREVIOUS_PAGE:
    if (payload.pageNumber <= 0){
      return state = {...state, pageNumber: 2}
    }
      return state = {...state, pageNumber: payload.pageNumber - 1}
    default:
      return state;
  }
};



