import React from 'react';

import pdf from '../images/resume2017.pdf';
import Resumeimage from '../images/resume2017.png';
import {Col} from 'react-bootstrap';

export default class Resume extends React.Component {
  constructor(props) {
  	super(props);
  	this.state = {
  		opacity: '0',
  	}
  }
  componentDidMount() {
        window.scrollTo(0, 0)
    setTimeout(function() { this.setState({opacity: '1'}); }.bind(this), '450');
  }
  render(){
    const margtop={
      opacity: this.state.opacity,
    }
    return(
      <div>
      <Col 
        md={5}
        mdOffset={1}
        sm={10}
        smOffset={1}
        xs={10}
        xsOffset={1}
        className='resume_downloader'>
          <a href={pdf} download="KevinChoiResume2017">
          Download
          </a>
      </Col>
      <Col 
        md={5} 
        mdOffset={0}
        sm={10}
        smOffset={1}
        xs={10}
        xsOffset={1}
        style={margtop}
        className='resume_image'
      >
        <img 
          src={Resumeimage} 
          className='project_fillimage' 
          alt="resume"
        />
		  </Col>
    </div>
    );
  }
}



