import React from 'react';
import Fromleft from './Fromleft';
import Fromright from './Fromright';
import {Row, Col} from 'react-bootstrap';

export default class Update extends React.Component {
  render() {
    return (
      <div className='update'>
        <div className='update_divider'></div>
        <Row>
          <Col
            md={2}
            mdOffset={2}
            sm={10}
            smOffset={1}
            xs={10}
            xsOffset={1}
            className='project_smallfont'
          >
            <Fromleft>
              {this.props.title}
            </Fromleft>
          </Col>
          <Col
            md={4}
            mdOffset={2}
            sm={10}
            smOffset={1}
            xs={10}
            xsOffset={1}
          >
          <Fromright>
            {this.props.children}
          </Fromright>
        </Col>
        </Row>
        <div className='update_divider'></div>
      </div>
    );
  }
}



