import React from 'react';

import {Col, Row} from 'react-bootstrap';
/*About component which is the white box under the banner in individual projects*/
export default class About extends React.Component {
  render() {
    return (
      <div>
        <div className='project_about'>
    			<div className='project_about__divider'></div>
    			<Row>
						<Col 
							md={2} 
							mdOffset={2} 
							sm={10} 
							smOffset={1} 
							xs={10}
							xsOffset={1} 
							className='project_smallfont'>
        				ABOUT
      				</Col>
						<Col 
							md={4}
							mdOffset={2}
							sm={10}
							smOffset={1}
							xs={10}
							xsOffset={1}
						>
      			{this.props.children}
      			</Col>
    			</Row>
    			<div className='project_about__divider'></div>
        </div>
      </div>
    );
  }
}



