import React from 'react';
import {Col, Row} from 'react-bootstrap';

import Bannercont from '../components/BannercontGaming';
import About from '../components/About';
import Backtoproj from '../components/Backtoproj';
import ProjectVideo from '../components/ProjectVideo';
import Dividemobilesmall from '../components/Dividemobilesmall';
import Dividedesktop from '../components/Dividedesktop';
import Fromleft from '../components/Fromleft';
import Fromright from '../components/Fromright';

import Bannerimage from '../images/gaming/sc.jpg';
import image1 from '../images/gaming/sc1.png';
import image2 from '../images/gaming/sc1.png';
import image3 from '../images/gaming/sc1.png';
import image4 from '../images/gaming/sc4.png';

export default class GamingSc extends React.Component {
	/*scroll to top on page load*/
	componentDidMount() {
		 window.scrollTo(0, 0)
	}
  render() {
    return (
  	<div className='project_wrapper'>
			<Bannercont
				bgimage={Bannerimage}
				line1="World of Warcraft"
				line2="2010"
				line3="Master League"
				
				/>
				<About>
        Achieved 'Master League', the highest division in the game aside from 'Grandmaster League', which is the top 200 players.
        Master league consists of the top 2% of players. http://us.battle.net/sc2/en/profile/847646/1/LighTs/
        </About>
				<div className='project_body'>
					<Row>
						<Col 
							md={3} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromleft 
								
							>
								<span 
									className='project_smallfont'
								>
									OVERVIEW
								</span>
								<br/><br/>
                Starcraft 2 is an RTS (Real Time Strategy) game where players can play as one of three races: terran, protoss, or zerg.
                I used the zerg race and played at a mid-masters level with approximately 120 to 160 APM (actions per minute).
							</Fromleft>
							<br/><br/>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={6} 
							mdOffset={1} 
							sm={10}
							smOffset={1}
							xs={10} 
							xsOffset={1}>
							<ProjectVideo poster={image1}/>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={7} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
							<Fromleft 
								
							>
								<img 
									src={image2} 
									className='project_fillimage'
									alt=""
								/>
							</Fromleft>
							<Dividedesktop/>
							<Dividemobilesmall/>
							<Row>
								<Col 
									md={6} 
									mdOffset={1} 
									sm={10} 
									smOffset={1} 
									xs={10} 
									xsOffset={1} 
									className='project_body_text'
								>
									<Fromleft 
										
									>
										Masters level was achieved four times directly after the release of the game, when the game's popularity was at its highest.
                    Initially, there was no 'Masters' or 'Grandmasters' division, with 'Diamond' being the highest achievable division, which I achieved
                    until the release of the 'Masters' division.
									</Fromleft>
								</Col>
							</Row>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={2} 
							mdOffset={1} 
							sm={4} 
							smOffset={4} 
							xs={10} 
							xsOffset={1}
						>
							<Fromright 
								
							>
								<img 
									src={image4} 
									className='project_fillimage' 
									alt=""
								/>
							</Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={8} 
							mdOffset={2} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
						<Fromleft 
							
						>
							<img 
								src={image3} 
								className='project_fillimage' 
								alt=""
							/>
						</Fromleft>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={4} 
							mdOffset={7} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromright 
								
							>
				      Starcraft 2 is known to be one of the most physically and mentally demanding games available with high 'actions per minute'
              requirements and complex gameplay.
              </Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
				</div>

				<Backtoproj direction="/gaming"/>
				</div>
    );
  }
}



