import React from 'react';
import {Row, Col} from 'react-bootstrap';
import Fromleft from './Fromleft';
import Fromright from './Fromright';

export default class Mobileimgs extends React.Component {
  render() {
    return (
      <div className='project_mobile_images'>
				<Row>
					<Col md={10} mdOffset={1} sm={10} smOffset={1} xs={10} xsOffset={1}>
						<Col md={2} mdOffset={0} sm={4} smOffset={0} xs={12} xsOffset={0}>
							<Fromleft>
							<img
								src={this.props.image1}
								className='project_fillimage'
								alt=''
							/>
							</Fromleft>
						</Col>
						<Col md={2} mdOffset={1} sm={4} smOffset={0} xs={12} xsOffset={0}>
							<img
								src={this.props.image2}
								className='project_fillimage'
								alt=''
							/>
						</Col>
						<Col md={2} mdOffset={1} sm={4} smOffset={0} xs={12} xsOffset={0}>
							<Fromright>
							<img
								src={this.props.image3}
								className='project_fillimage'
								alt=""
							/>
							</Fromright>
						</Col>
					</Col>
				</Row>
      </div>
    );
  }
}



