import React from 'react';

import {Switch, Route} from 'react-router-dom';

import './App.css';
import './Home.css';
import './Menu.css';
import './Projects.css';

import Menu from './menu/MenuWrap';

import Background from './home/Background';

import Projects from './pages/Projects';
import Gaming from './pages/Gaming';

import ProjectUwloo from './pages/ProjectUwloo';
import ProjectTcfs from './pages/ProjectTcfs';
import ProjectWildcat from './pages/ProjectWildcat';
import ProjectDiscord from './pages/ProjectDiscord';

import ProjectPcme from './pages/ProjectPcme';
import ProjectLeida from './pages/ProjectLeida';
import ProjectLastfm from './pages/ProjectLastfm';
import ProjectWns from './pages/ProjectWns';

import ProjectDrjart from './pages/ProjectDrjart';
import ProjectPhpgame from './pages/ProjectPhpgame';
import ProjectLibrary from './pages/ProjectLibrary';
import ProjectTemplate from './pages/ProjectTemplate';

import GamingWow from './pages/GamingWow';
import GamingHs from './pages/GamingHs';
import GamingLol from './pages/GamingLol';
import GamingOw from './pages/GamingOw';
import GamingSc from './pages/GamingSc';
import GamingMisc from './pages/GamingMisc';

import Resume from './pages/Resume';

import Logo from './components/Logo';
import Contact from './components/Contact';
import CheckScroll from './components/CheckScroll';

export default class App extends React.Component {

  render() {
    return (
      <div className="App">
        <CheckScroll />
        <Route exact path={"/"} children={({ match }) => (
		      <div>
		        <Background
              isHome={Boolean(match) ? true : false}
		        />
            <Logo
              isHome={Boolean(match) ? true : false}
		        />
		      </div>
		    )}/>


        <Route exact path={"/projects"} children={({ match }) => (
		      <div>
		        <Projects
              isProjects={Boolean(match) ? true : false}
		        />
		      </div>
		    )}/>

        <Route exact path={"/gaming"} children={({ match }) => (
		      <div>
		        <Gaming
              isGaming={Boolean(match) ? true : false}
		        />
		      </div>
		    )}/>


        <Contact/>

        <Switch>
          <Route
            exact path="/projects/uwloo"
            render={(props) =>
            <ProjectUwloo
              {...props}
            />}
          />
          <Route
            exact path="/projects/wildcat"
            render={(props) =>
            <ProjectWildcat
              {...props}
            />}
          />
          <Route
            exact path="/projects/tcfs"
            render={(props) =>
            <ProjectTcfs
              {...props}
            />}
          />
          <Route
            exact path="/projects/discord"
            render={(props) =>
            <ProjectDiscord
              {...props}
            />}
          />
          <Route
            exact path="/projects/lastfm"
            render={(props) =>
            <ProjectLastfm
              {...props}
            />}
          />
          <Route
            exact path="/projects/powerpcme"
            render={(props) =>
            <ProjectPcme
              {...props}
            />}
          />
          <Route
            exact path="/projects/leida"
            render={(props) =>
            <ProjectLeida
              {...props}
            />}
          />
          <Route
            exact path="/projects/wns"
            render={(props) =>
            <ProjectWns
              {...props}
            />}
          />
          <Route
            exact path="/projects/drjart"
            render={(props) =>
            <ProjectDrjart
              {...props}
            />}
          />
          <Route
            exact path="/projects/phpgame"
            render={(props) =>
            <ProjectPhpgame
              {...props}
            />}
          />
          <Route
            exact path="/projects/library"
            render={(props) =>
            <ProjectLibrary
              {...props}
            />}
          />
          <Route
            exact path="/projects/template1"
            render={(props) =>
            <ProjectTemplate
              {...props}
            />}
          />
          <Route
            exact path="/gaming/wow"
            render={(props) =>
            <GamingWow
              {...props}
            />}
          />
          <Route
            exact path="/gaming/hs"
            render={(props) =>
            <GamingHs
              {...props}
            />}
          />
          <Route
            exact path="/gaming/lol"
            render={(props) =>
            <GamingLol
              {...props}
            />}
          />
          <Route
            exact path="/gaming/sc"
            render={(props) =>
            <GamingSc
              {...props}
            />}
          />
          <Route
            exact path="/gaming/ow"
            render={(props) =>
            <GamingOw
              {...props}
            />}
          />
          <Route
            exact path="/gaming/misc"
            render={(props) =>
            <GamingMisc
              {...props}
            />}
          />
          <Route
            exact path="/resume"
            render={(props) =>
            <Resume
              {...props}
            />}
          />
        </Switch>

        <Menu/>
      </div>
    );
  }
}




