export const NEXT_PAGE = Symbol('NEXT_PAGE');
export const PREVIOUS_PAGE = Symbol('PREVIOUS_PAGE');
export const RESIZING = Symbol('RESIZING');

export const nextPage = (pageNumber) =>{
  return{
    type: NEXT_PAGE,
    pageNumber,
  };
}

export const previousPage = (pageNumber) =>{
  return{
    type: PREVIOUS_PAGE,
    pageNumber,
  };
}



