import React from 'react';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import * as scrollActions from '../actions/scroll';

class CheckScroll extends React.Component {
  constructor(props){
    super(props);
		this.scroller = this.scroller.bind(this)
	}
  componentWillMount(){
    this.scroller();
    window.addEventListener("scroll", this.scroller);
    window.addEventListener("resize", this.scroller);
  }
  scroller() {
    let supportPageOffset = window.pageXOffset !== undefined;
    let isCSS1Compat = ((document.compatMode || '') === 'CSS1Compat');
    let scroll = {
        x: supportPageOffset ? window.pageXOffset : isCSS1Compat ? document.documentElement.scrollLeft : document.body.scrollLeft,
        y: supportPageOffset ? window.pageYOffset : isCSS1Compat ? document.documentElement.scrollTop : document.body.scrollTop
    };
    this.props.scrollActions.checkScroll(scroll.y);
  }

  render(){
    return null;
  }
}

export default connect(
  (state, ownProps) => ({
    scrollamount: state.scroll.scrollAmount,
  }),
  dispatch => ({
    scrollActions: bindActionCreators(scrollActions, dispatch),
  }),
)(CheckScroll);



