import React from 'react';

import {Row, Col} from 'react-bootstrap';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import * as menuActions from '../actions/gaming';
import classNames from 'classnames';

import wow from '../images/thumbnails/wow.jpg';
import hs from '../images/thumbnails/hs.jpg';
import sc from '../images/thumbnails/sc.jpg';
import ow from '../images/thumbnails/ow.jpg';
import lol from '../images/thumbnails/lol.jpg';
import misc from '../images/thumbnails/misc.jpg';

import Thumbnail from '../components/Thumbnail';
import debounce from 'debounce';

class Gaming extends React.Component{
  constructor(props) {
    super(props);
    this.state = {
      transition:".5s ease-out"
    }
    this.bgresize=this.bgresize.bind(this)
    this.restoretrans = debounce(this.restoretrans.bind(this), 100);
  }
	bgresize(){
		this.setState({
			transition:"0s"
		}, () => {
			this.restoretrans();
	});
	}
	/*when resizing the window, the transition time is 0s so the blackbg doesn't bounce around, then it is set back to 600ms with a debounce so there is delay*/

	restoretrans(){
		this.setState({
			transition:".5s ease-out"
		})
	}

  componentDidMount(){
    this.props.menuActions.nextPage(0)
    window.addEventListener("resize", this.bgresize);
  }

  componentWillUnmount(){
    window.removeEventListener("resize", this.bgresize);
  }

  render(){
    const transitiontime={
      WebkitTransition: this.state.transition,
			MozTransition: this.state.transition,
			OTransition: this.state.transition,
			transition:this.state.transition,
    }
    const {
      isGaming,
      currentPage,
    } = this.props;


    const projectsWrapper = classNames(
      'projects_wrapper',
      {
        'projects_wrapper--display': isGaming,
      }
    );
    const projectThumbnail1 = classNames(
      'projects_thumbnail1',
      {
        'projects_thumbnail--display': isGaming,
      }
    );
    const projectThumbnail2 = classNames(
      'projects_thumbnail2',
      {
        'projects_thumbnail--display': isGaming,
      }
    );
    const projectThumbnail3 = classNames(
      'projects_thumbnail3',
      {
        'projects_thumbnail--display': isGaming,
      }
    );
    const projectThumbnail4 = classNames(
      'projects_thumbnail4',
      {
        'projects_thumbnail--display': isGaming,
      }
    );
    const projectLeft = classNames(
      'projects_left',
      {
        'projects_left--display': isGaming,
      }
    )
    const projectRight = classNames(
      'projects_right',
      {
        'projects_right--display': isGaming,
      }
    )
    const projectTextLeft = classNames(
      'project_thumbnail__text__left',
      {
        'project_thumbnail__text__left--display': isGaming,
      }
    )
    const projectTextLeft2 = classNames(
      'project_thumbnail__text__left2',
      {
        'project_thumbnail__text__left2--display': isGaming,
      }
    )
    const projectTextRight = classNames(
      'project_thumbnail__text__right',
      {
        'project_thumbnail__text__right--display': isGaming,
      }
    )

    return(
      <div className={projectsWrapper}>
        <Row>
          <Col md={6} className={projectThumbnail1} style={transitiontime}>
            <Thumbnail
            bgimage={currentPage === 1 ? wow : ow}

            link={currentPage === 1 ? '/gaming/wow' : '/gaming/ow'}

            />
          </Col>
          <Col md={2} mdOffset={1} sm={12} smOffset={0} xs={12} xsOffset={0}>
            {currentPage === 1
            ?
            <div className={projectTextLeft}>
            <strong>World of Warcraft</strong><br/>
            Peak: 8th Globally<br/>
            2008
            </div>
            :
            <div className={projectTextLeft}>
            <strong>Overwatch</strong><br/>
            Diamond League<br/>
            2014
            </div>
            }
          </Col>
        </Row>
        <Row>
          <Col md={6} mdOffset={2} className={projectThumbnail2}>
            <Thumbnail
            bgimage={currentPage === 1 ? hs : misc}

            link={currentPage === 1 ? '/gaming/hs' : '/gaming/misc'}
            />
          </Col>
          <Col md={2} mdOffset={1} sm={12} smOffset={0} xs={12} xsOffset={0}>
            {currentPage === 1
            ?
            <div className={projectTextLeft2}>
              <strong>Hearthstone</strong><br/>
              19th in NA, 90th in NA<br/>
              May 2017 / January 2018
            </div>
            :
            <div className={projectTextLeft2}>
            <strong>Misc Games</strong>
            </div >
            }
          </Col>
        </Row>
        <Row>
          <Col md={3}>
              {currentPage === 1
              ?
              <div className={projectTextRight}>
                <strong>Starcraft II</strong><br/>
                Master League - Top 2%<br/>
                2010
              </div>
              :

              <div className={projectTextRight}>
                <strong>World of Warcraft</strong><br/>
                Peak: 8th Globally<br/>
                2008
              </div>

              }
            </Col>
          <Col md={6} mdOffset={1} className={projectThumbnail3}>
            <Thumbnail
            bgimage={currentPage === 1 ? sc : wow}

            link={currentPage === 1 ? '/gaming/sc' : '/gaming/wow' }
            />
          </Col>
        </Row>
        <Row>
        <Col md={3} mdOffset={2}>
            {currentPage === 1
            ?
            <div className={projectTextRight}>
              <strong>League of Legends</strong><br/>
              Diamond League - Top 0.5%<br/>
              2010 - 2017
            </div>
            :
            <div className={projectTextRight}>
              <strong>Hearthstone</strong><br/>
              19th in NA, 90th in NA<br/>
              May 2017 / January 2018
            </div>

            }
          </Col>
          <Col md={6} mdOffset={1} className={projectThumbnail4}>
          <Thumbnail
          bgimage={currentPage === 1 ? lol : hs}

          link={currentPage === 1 ? '/gaming/lol' : '/gaming/hs'}
          />
          </Col>
        </Row>

        <div
          className={projectLeft}
          onClick={()=>this.props.menuActions.previousPage(currentPage)
        }>
        &lt;
        </div>
        <div
          className={projectRight}
          onClick={()=>this.props.menuActions.nextPage(currentPage)}
        >
        &gt;
        </div>
      </div>
    )
  }
}

export default connect(
  (state, ownProps) => ({
    isHidden:state.gaming.isHidden,
    currentPage: state.gaming.pageNumber,
  }),
  dispatch => ({
    menuActions: bindActionCreators(menuActions, dispatch),
  }),
)(Gaming);



