import React from 'react';
import {Col, Row} from 'react-bootstrap';

import Bannercont from '../components/BannercontGaming';
import About from '../components/About';
import Backtoproj from '../components/Backtoproj';
import ProjectVideo from '../components/ProjectVideo';
import Dividemobilesmall from '../components/Dividemobilesmall';
import Dividedesktop from '../components/Dividedesktop';
import Fromleft from '../components/Fromleft';
import Fromright from '../components/Fromright';

import Bannerimage from '../images/gaming/hs.jpg';
import image1 from '../images/gaming/hs1.png';
import image2 from '../images/gaming/hs2.png';
import image3 from '../images/gaming/hs3.png';
import image4 from '../images/gaming/hs4.jpg';

export default class GamingWow extends React.Component {
	/*scroll to top on page load*/
	componentDidMount() {
		 window.scrollTo(0, 0)
	}
  render() {
    return (
  	<div className='project_wrapper'>
			<Bannercont
				bgimage={Bannerimage}
				line1="World of Warcraft"
				line2="May 2017"
				line3="19th in North America"
				/>
				<About>
        'Infinite Arena' was achieved since release in 2014, meaning a 66%-70% win rate in the draft format (average of 6-7 wins per arena).
        With this winrate, thousands of 'card packs' have been accumulated alongside gold, which is the in-game currency.
        Additionally, 'Legend' rank, which is the highest in-game rank has been achieved	multiple times.<br/>
				Achieved 19th place in North America in May 2017.
				<br/>
				<a href='http://us.battle.net/hearthstone/en/blog/20838076/top-hearthstone-players-may-2017-6-8-2017'
				target='_blank'
				rel="noopener noreferrer"
				>
				http://us.battle.net/hearthstone/en/blog/20838076/top-hearthstone-players-may-2017-6-8-2017
				</a>
				<br/>
				Achieved 90th place in North America in January 2018
				<br/>
				<a href='https://playhearthstone.com/en-us/blog/21513836'
				target='_blank'
				rel="noopener noreferrer"
				>
				https://playhearthstone.com/en-us/blog/21513836
				</a>
        </About>
				<div className='project_body'>
					<Row>
						<Col
							md={3}
							mdOffset={1}
							sm={10}
							smOffset={1}
							xs={10}
							xsOffset={1}
							className='project_body_text'
						>
							<Fromleft>
								<span
									className='project_smallfont'
								>
									OVERVIEW
								</span>
								<br/><br/>
                Hearthstone, originally known as Hearthstone: Heroes of Warcraft, is a
                free-to-play online collectible card video game developed and published by Blizzard Entertainment.
							</Fromleft>
							<br/><br/>
						</Col>
						<Dividemobilesmall/>
						<Col
							md={6}
							mdOffset={1}
							sm={10}
							smOffset={1}
							xs={10}
							xsOffset={1}>
							<ProjectVideo poster={image1}/>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col
							md={7}
							mdOffset={1}
							sm={10}
							smOffset={1}
							xs={10}
							xsOffset={1}
						>
							<Fromleft>
								<img
									src={image2}
									className='project_fillimage'
									alt=""
								/>
							</Fromleft>
							<Dividedesktop/>
							<Dividemobilesmall/>
							<Row>
								<Col
									md={6}
									mdOffset={1}
									sm={10}
									smOffset={1}
									xs={10}
									xsOffset={1}
									className='project_body_text'
								>
									<Fromleft>
										The game has been favorably reviewed by critics and proven successful for Blizzard. As of May 2017,
                    Blizzard has reported more than 70 million Hearthstone players. The game has become popular as an eSport,
                    with cash-prize tournaments hosted by Blizzard and other organizers.
									</Fromleft>
								</Col>
							</Row>
						</Col>
						<Dividemobilesmall/>
						<Col
							md={2}
							mdOffset={1}
							sm={4}
							smOffset={4}
							xs={10}
							xsOffset={1}
						>
							<Fromright>
								<img
									src={image4}
									className='project_fillimage'
									alt=""
								/>
							</Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col
							md={8}
							mdOffset={2}
							sm={10}
							smOffset={1}
							xs={10}
							xsOffset={1}
						>
						<Fromleft>
							<img
								src={image3}
								className='project_fillimage'
								alt=""
							/>
						</Fromleft>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col
							md={4}
							mdOffset={7}
							sm={10}
							smOffset={1}
							xs={10}
							xsOffset={1}
							className='project_body_text'
						>
						</Col>
					</Row>
					<Dividedesktop/>
				</div>

				<Backtoproj direction="/gaming"/>
				</div>
    );
  }
}



