import React from 'react';
import {Col, Row} from 'react-bootstrap';

import Bannercont from '../components/Bannercont';
import About from '../components/About';
import Backtoproj from '../components/Backtoproj';
import Mobileimgs from '../components/Mobileimgs';
import ProjectVideo from '../components/ProjectVideo';
import Dividemobilesmall from '../components/Dividemobilesmall';
import Dividedesktop from '../components/Dividedesktop';
import Fromleft from '../components/Fromleft';
import Fromright from '../components/Fromright';

import Bannerimage from '../images/leida/banner.jpg';
import Video from '../images/leida/leida.mp4';
import image1 from '../images/leida/1.jpg';
import image2 from '../images/leida/2.jpg';
import image3 from '../images/leida/3.jpg';
import image4 from '../images/leida/4.jpg';
import mobimg1 from '../images/leida/5.jpg';
import mobimg2 from '../images/leida/6.jpg';
import mobimg3 from '../images/leida/7.jpg';

export default class ProjectLastfm extends React.Component {
	/*scroll to top on page load*/
	componentDidMount() {
		 window.scrollTo(0, 0)
	}
  render() {
    return (
  	<div className='project_wrapper'>
			<Bannercont
				bgimage={Bannerimage}
				line1="ideal leida"
				line2="2016"
				line3="HTML CSS Javascript PHP"
				line4="http://nynyer.com/project/idealleida"
				
				/>
				<About>
          Edward Leida was trained as both an Industrial and Graphic Designer.
          During the course of his career, he has done extensive work in the areas of
          Corporate Identity, Editorial Design, Book Design and Advertising. He served
          as the Design Director of W Magazine and during his tenure, Leida redesigned
          W in 1991 as well as designed and established the formats for Jane Magazine,
          Details Magazine and Women's Wear Daily.
				</About>
				<div className='project_body'>
					<Row>
						<Col 
							md={3} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromleft 
								
							>
								<span 
									className='project_smallfont'
								>
									OVERVIEW
								</span>
								<br/><br/>
                The site was created using design .psd schematics. The background was
                built as a full screen carousel aside from the side menu, which controls 3
                layers of slide-in panels.
                <br/><br/>
                The topmost panel is a 'Hello' panel which works as a description page for Edward Leida.
                The bottom panel shows a collection of thumbnails which each link to a slide-in of
                a third middle panel which contains a carousel and description. Clicking on various
                panels causes other panels to interact with the click, and clicking on side menu items
                displays different thumbnails.
							</Fromleft>
							<br/><br/>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={6} 
							mdOffset={1} 
							sm={10}
							smOffset={1}
							xs={10} 
							xsOffset={1}>
							<ProjectVideo video={Video} poster={image1}/>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={7} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
							<Fromleft 
								
							>
								<img 
									src={image2} 
									className='project_fillimage'
									alt=""
								/>
							</Fromleft>
							<Dividedesktop/>
							<Dividemobilesmall/>
							<Row>
								<Col 
									md={6} 
									mdOffset={1} 
									sm={10} 
									smOffset={1} 
									xs={10} 
									xsOffset={1} 
									className='project_body_text'
								>
									<Fromleft 
										
									>
										The thumbnails can be shown as a thumbnail view or a list view,
                    and the thumbnails can be sorted by project title, category, or date.
                    The Kirby user interface was implemented such that a user could easily
                    add new projects which correspond to the various selections in the side
                    menu. In addition to adding the title and description of each project,
                    the user can add a thumbnail image and up to ten carousel images."
                    para4="All parts of the site were designed to be responsive from desktop
                     views, tablet views, and phone views.
									</Fromleft>
								</Col>
							</Row>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={2} 
							mdOffset={1} 
							sm={4} 
							smOffset={4} 
							xs={10} 
							xsOffset={1}
						>
							<Fromright 
								
							>
								<img 
									src={image4} 
									className='project_fillimage' 
									alt=""
								/>
							</Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={8} 
							mdOffset={2} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
						<Fromleft 
							
						>
							<img 
								src={image3} 
								className='project_fillimage' 
								alt=""
							/>
						</Fromleft>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={4} 
							mdOffset={7} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromright 
								
							>
				        The Kirby file-based content management system (CMS) was integrated into the website for a intuitive user experience.
                HTML, CSS, Javascript, and PHP were used in Atom, an open-source and free text editor was used as an
                IDE (integrated development environment) to follow templates which were provided as .psd files.
                Bootstrap, a mobile first front-end framework was used in the creation of carousels. Carousels were each customized
                for use in both the main background and in each individual project. Javascript was used to check the number of
                slides that were input through the Kirby CMS and PHP to make each carousel function correctly between a range of 1 to 10 slides.
              </Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
				</div>
				<Mobileimgs 
					
					image1={mobimg1}
					image2={mobimg2}
					image3={mobimg3}
				/>
				<Backtoproj direction="/projects"/>
				</div>
    );
  }
}



