import React from 'react';

export default class ProjectVideo extends React.Component{
  render(){
    const fillimage={
      width:"100%",
    }
    return(
      <video 
        loop 
        autoPlay="autoplay" 
        style={fillimage} 
        poster={this.props.poster}
      >
      {
        this.props.video !== ""
        ?
        <source 
          src={this.props.video} 
          type="video/mp4" 
        />
        :
        null
      }
      </video>
    )
  }
}


