import React from 'react';
import {Col, Row} from 'react-bootstrap';

import Bannercont from '../components/BannercontGaming';
import About from '../components/About';
import Backtoproj from '../components/Backtoproj';
import ProjectVideo from '../components/ProjectVideo';
import Dividemobilesmall from '../components/Dividemobilesmall';
import Dividedesktop from '../components/Dividedesktop';
import Fromleft from '../components/Fromleft';
import Fromright from '../components/Fromright';

import Bannerimage from '../images/gaming/lol.jpg';
import image1 from '../images/gaming/lol1.png';
import image2 from '../images/gaming/lol2.png';
import image3 from '../images/gaming/lol3.png';
import image4 from '../images/gaming/lol4.png';

export default class GamingLol extends React.Component {
	/*scroll to top on page load*/
	componentDidMount() {
		 window.scrollTo(0, 0)
	}
  render() {
    return (
  	<div className='project_wrapper'>
			<Bannercont
				bgimage={Bannerimage}
				line1="League of Legends"
				line2="2010 - 2017"
				line3="Diamond League"
				/>
				<About>
        League of Legends is a MOBA (Multiplayer Online Battle Arena) game with over 100 million active players as of September
        2016 according to Forbes. The rank of Diamond (Approximately top 1.5% was achieved for 3 consecutive seasons (Season 4,5,6),
        each of which are approximately one year in length. http://na.op.gg/summoner/userName=shodyra
        </About>
				<div className='project_body'>
					<Row>
						<Col 
							md={3} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromleft 
								
							>
								<span 
									className='project_smallfont'
								>
									OVERVIEW
								</span>
								<br/><br/>
                The ranked system in League of Legends is primarily built around 5v5 gameplay.
                Each season introduces new gameplayer changes and ranking systems, but the core of the game remains the same.
                The world tournament prize pool for 2016 was approximately 5 million dollars and Riot Games's revenue from
                League of Legends in 2016 was approximately 1.6 billion dollars.
							</Fromleft>
							<br/><br/>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={6} 
							mdOffset={1} 
							sm={10}
							smOffset={1}
							xs={10} 
							xsOffset={1}>
							<ProjectVideo poster={image1}/>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={7} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
							<Fromleft 
								
							>
								<img 
									src={image2} 
									className='project_fillimage'
									alt=""
								/>
							</Fromleft>
							<Dividedesktop/>
							<Dividemobilesmall/>
							<Row>
								<Col 
									md={6} 
									mdOffset={1} 
									sm={10} 
									smOffset={1} 
									xs={10} 
									xsOffset={1} 
									className='project_body_text'
								>
									<Fromleft 
										
									>
									Despite constant changes in gameplay, Diamond was reached across three consecutive seasons by playing a
                  'fill' role, which is to fill in any position that the team requires. League of Legends features 5 positions: Top,
                   Jungle, Middle, Marksman, and Support. As a 'fill' player, I developed reasonable ability in every
                    role as the teams required.
									</Fromleft>
								</Col>
							</Row>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={2} 
							mdOffset={1} 
							sm={4} 
							smOffset={4} 
							xs={10} 
							xsOffset={1}
						>
							<Fromright 
								
							>
								<img 
									src={image4} 
									className='project_fillimage' 
									alt=""
								/>
							</Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={8} 
							mdOffset={2} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
						<Fromleft 
							
						>
							<img 
								src={image3} 
								className='project_fillimage' 
								alt=""
							/>
						</Fromleft>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={4} 
							mdOffset={7} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromright 
								
							>

              </Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
				</div>

				<Backtoproj direction="/gaming"/>
				</div>
    );
  }
}



