import React from 'react';
import {Col, Row} from 'react-bootstrap';

import Bannercont from '../components/Bannercont';
import About from '../components/About';
import Backtoproj from '../components/Backtoproj';
import Mobileimgs from '../components/Mobileimgs';
import ProjectVideo from '../components/ProjectVideo';
import Dividemobilesmall from '../components/Dividemobilesmall';
import Dividedesktop from '../components/Dividedesktop';
import Fromleft from '../components/Fromleft';
import Fromright from '../components/Fromright';

import Bannerimage from '../images/tcfs/toocool.jpg';
import Video from '../images/tcfs/tc4s.mp4';
import image1 from '../images/tcfs/1.jpg';
import image2 from '../images/tcfs/2.jpg';
import image3 from '../images/tcfs/3.jpg';
import image4 from '../images/tcfs/4.jpg';
import mobimg1 from '../images/tcfs/5.jpg';
import mobimg2 from '../images/tcfs/6.jpg';
import mobimg3 from '../images/tcfs/7.jpg';

export default class ProjectTcfs extends React.Component {
	/*scroll to top on page load*/
	componentDidMount() {
		 window.scrollTo(0, 0)
	}
  render() {
    return (
  	<div className='project_wrapper'>
			<Bannercont
				bgimage={Bannerimage}
				line1="too cool for school"
				line2="2016-2017"
				line3="HTML CSS Javascript"
				line4="http://toocoolforschool.us/"
				
				/>
				<About>
          Too Cool For School is an artistic cosmetic brand focused
          on creative experimentation and contemporary culture. Too Cool For
          School was created in New York City. The artwork for the first
          artist-collaborative packaging was created simultaneously with a
          freelance illustrator in London.
				</About>
				<div className='project_body'>
					<Row>
						<Col 
							md={3} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromleft 
								
							>
								<span 
									className='project_smallfont'
								>
									OVERVIEW
								</span>
								<br/><br/>
                Various additions were made to the site including the following:
                a responsive closeable overlay on page load, editing of submenus, editing
                of products and product descriptions, and a responsive customized carousel.
                <br/><br/>
                The overlay was designed along specifications regarding the border
                spacing outside the overlay and the spacing between the edge of the
                overlay and the image/text as seen above in slides 4 and 5.
							</Fromleft>
							<br/><br/>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={6} 
							mdOffset={1} 
							sm={10}
							smOffset={1}
							xs={10} 
							xsOffset={1}>
							<ProjectVideo video={Video} poster={image1}/>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={7} 
							mdOffset={1} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
							<Fromleft 
								
							>
								<img 
									src={image2} 
									className='project_fillimage'
									alt=""
								/>
							</Fromleft>
							<Dividedesktop/>
							<Dividemobilesmall/>
							<Row>
								<Col 
									md={6} 
									mdOffset={1} 
									sm={10} 
									smOffset={1} 
									xs={10} 
									xsOffset={1} 
									className='project_body_text'
								>
									<Fromleft 
										
									>
											Existing content in the submenu under 'Products' was edited in both
                      content and design. Content under the 'Inspirations' was created with the
                      same template as the Products submenu and the mobile version hamburger menu
                      was edited such that the selection graphic moved upon submenu selection, and
                      javascript was used to handle hiding and showing the submenu content in the
                      mobile hamburger menu.
                      <br/><br/>
                      The carousel as seen above in slide 2 was created with desktop and
                      mobile specifications using bootstrap. The bootstrap carousel was then
                      edited to meet design standards.
									</Fromleft>
								</Col>
							</Row>
						</Col>
						<Dividemobilesmall/>
						<Col 
							md={2} 
							mdOffset={1} 
							sm={4} 
							smOffset={4} 
							xs={10} 
							xsOffset={1}
						>
							<Fromright 
								
							>
								<img 
									src={image4} 
									className='project_fillimage' 
									alt=""
								/>
							</Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={8} 
							mdOffset={2} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1}
						>
						<Fromleft 
							
						>
							<img 
								src={image3} 
								className='project_fillimage' 
								alt=""
							/>
						</Fromleft>
						</Col>
					</Row>
					<Dividedesktop/>
					<Dividemobilesmall/>
					<Row>
						<Col 
							md={4} 
							mdOffset={7} 
							sm={10} 
							smOffset={1} 
							xs={10} 
							xsOffset={1} 
							className='project_body_text'
						>
							<Fromright 
								
							>
				        XAMPP, an open source web server solution was used for development
                testing for each portion of editing.
                Atom, an open-source and free text editor was used as an IDE (integrated
                development environment) to follow templates which were provided as .psd files.
                Bootstrap, a mobile first front-end framework was used in making the carousel.</Fromright>
						</Col>
					</Row>
					<Dividedesktop/>
				</div>
				<Mobileimgs 
					
					image1={mobimg1}
					image2={mobimg2}
					image3={mobimg3}
				/>
				<Backtoproj direction="/projects"/>
				</div>
    );
  }
}



