import React from 'react';
import { connect } from 'react-redux';

class FromRight extends React.Component {
  constructor(props) {
	 super(props);
	 this.state = {
     opacity: "0",
     trans3d: "translate3d(100px, 0px, 0px)",
     trans: "translate(100px, 0px)",
   }
   this.slidein=this.slidein.bind(this)
   this.trueoffset = this.trueoffset.bind(this)
 }
  /*fades in the banner*/
  componentDidMount() {
   window.addEventListener("scroll", this.slidein);
   window.addEventListener("resize", this.slidein);
   this.slidein();
  }

  trueoffset( elem )
  {
      var offsetTop = 0;
      while (elem) {
        if ( !isNaN( elem.offsetTop ) )
        {
            offsetTop += elem.offsetTop;
            elem = elem.offsetParent;
        }
      }
      return offsetTop;
  }

  slidein(){

    try{
      if ((((this.trueoffset(this.refs.loc)) - (window.innerHeight*.8)) < this.props.scrollamount) && (((this.trueoffset(this.refs.loc)) + (window.innerHeight*.5))>this.props.scrollamount)){
        this.setState({
          opacity:"1",
          trans3d:"translate3d(0px, 0px, 0px)",
          trans:"translate(0px, 0px)",
        });
      }
     else{
        this.setState({
          opacity: "0",
          trans3d: "translate3d(100px, 0px, 0px)",
          trans: "translate(100px, 0px)",
        });
      }
    }
    catch(err) {
  }
}

  render() {

    const objectstyle={
      opacity:this.state.opacity,
      MozTransition: "all 600ms ease-in",
      WebkitTransition: "all 600ms ease-in",
      OTransition: "all 600ms ease-in",
      transition: "all 600ms ease-in",
      MozTransform: this.state.trans3d,
      WebkitTransform: this.state.trans3d,
      OTransform: this.state.trans,
      MsTransform: this.state.trans,
      transform: this.state.trans3d,
    }
    return (
      <div ref="loc" style={objectstyle}>
      {this.props.children}
      </div>
    );
  }
}

export default connect(
  (state, ownProps) => ({
    scrollamount: state.scroll.scrollAmount,
  }),
  dispatch => ({
  }),
)(FromRight);



